html {
  font-size: 62.5%;
  /* 1 */
}

@keyframes animation-background-gradient {
  0% {
    background-position: 0% 99%;
  }
  50% {
    background-position: 100% 2%;
  }
  100% {
    background-position: 0% 99%;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes zoomfadeIn {
  0% {
    opacity: 0;
    visibility: hidden;
    transform: scale(0.5);
  }
  100% {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
  }
}

@keyframes zoomfadeOut {
  0% {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    visibility: hidden;
    transform: scale(0.5);
  }
}

@keyframes rightfadeIn {
  0% {
    opacity: 0;
    transform: translateX(-100px);
  }
  100% {
    opacity: 1;
    transform: scale(1);
    transform: translateY(0);
  }
}

@keyframes leftfadeIn {
  0% {
    opacity: 0;
    transform: translateX(100px);
  }
  100% {
    opacity: 1;
    transform: scale(1);
    transform: translateY(0);
  }
}

.left-text {
  text-align: left;
}

.right-text {
  text-align: right;
}

.center-text {
  text-align: center;
}

.vertical-align--relative {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.vertical-align--absolute {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.display-table {
  display: table;
}

.valign-middle {
  display: table-cell;
  vertical-align: middle;
}

.position--relative {
  position: relative;
}

.position--absolute {
  position: absolute;
}

.position--static {
  position: static;
}

.position--fixed {
  position: fixed;
}

.gradhov:after {
  content: '';
  height: 100%;
  width: 100%;
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  opacity: 0.7;
  background: #f4a6e1;
  background: linear-gradient(35deg, #f4a6e1 0%, #88d8f0 45%, #63acef 75%, #88d8f0 100%);
  background-size: 500% 500%;
  animation: animation-background-gradient 30s ease infinite;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  margin: 0 auto;
  mix-blend-mode: multiply;
  /*
        opacity: 0;
        transition: .4s;
        */
}

.no-margin {
